$mobile_width: 767px;
$tablet_width: 1056px;

body {
	font-family: $mrEaves;
}

h1 {
	margin-bottom: 15px;
	font-size: 65px;
}

h2 {
	margin-bottom: 15px;
	font-size: 45px;
}

h3 {
	margin-bottom: 15px;
	font-size: 35px;
}

h4 {
	margin-bottom: 15px;
	font-size: 29px;
	font-weight: 600;
	line-height: 1.2em;
}

h5 {
	margin-bottom: 15px;
	font-size: 20px;
}

h6 {
	margin-bottom: 15px;
	font-size: 15px;
}

p {
	margin-bottom: 15px;
	font-size: 20px;
	line-height: 1.4em;
	color: $darkGray;
	// letter-spacing: 0.1em;
}

a {
	color: $skyBlue;
}

em {
	font-style: italic;
}

#content {
	padding-top: 50px;
}

.content-margins {
	max-width: 1024px;
	width: 90%;
	margin: 0 auto;
	&.wide {
		width: 95%;
		max-width: 1280px;
	}
}

.bg-center {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

.full-width-title {
	width: 100%;
	text-align: center;
	background-color: $darkGray;
	color: white;
	padding: 15px 0;
	margin-bottom: 25px;
	h4 {
		text-transform: uppercase;
		font-size: 20px;
		font-weight: 100;
		margin-bottom: 0px;
	}
}

.section-title {
	text-transform: uppercase;
	font-family: $mrsEaves;
	font-size: 20px;
	color: $darkGray;
	letter-spacing: 0.3em;
	font-weight: 500;
	margin-bottom: 60px;
}

.button,
button {
	background-color: $skyBlue;
	border: 0px;
	border-radius: 70px;
	padding: 20px 70px;
	color: white;
	font-style: normal;
	text-transform: uppercase;
	text-decoration: none;
	display: inline-block;
	font-size: 20px;
}

.button-cnt {
	margin: 20px 0;
	display: block;
	text-align: center;
}

.page-content {
	a {
		color: $skyBlue;
	}
	.wpcf7 {
		.form-inputs {
			width: 100%;
			display: flex;
			justify-content: space-between;

			p {
				width: calc(50% - 20px);
			}

			@media (max-width: 767px) {
				flex-direction: column;
				p {
					width: 100%;
				}
			}
		}
		label {
			width: 100%;
			display: block;
			text-transform: uppercase;
			font-weight: 800;
			.wpcf7-form-control-wrap {
				width: 100%;
				display: block;
				margin-top: 15px;
			}
			input {
				width: 100%;
				border: 1px solid $darkGray;
				border-radius: 5px;
				padding: 10px;
			}
		}
		textarea {
			width: 100%;
			padding: 10px;
			border: 1px solid $darkGray;
			border-radius: 5px;
		}
		.wpcf7-validation-errors {
			display: none !important;
		}
		.wpcf7-not-valid-tip {
			font-size: 14px;
			margin-top: 5px;
			font-weight: 700;
		}
		input[type="submit"] {
			display: inline-block;
			margin: 0 auto;
		}
		.center {
			input[type="submit"] {
				background-color: $skyBlue;
				border: 0px;
				border-radius: 70px;
				padding: 20px 70px;
				color: white;
				font-style: normal;
				text-transform: uppercase;
				text-decoration: none;
				display: inline-block;
				font-size: 20px;
			}
		}
	}
}

.center {
	text-align: center;
}



